import axios from 'axios'
import React from 'react'
import { useContext } from 'react'

import { cartContext } from '../../context/cartcontext'
import toast from 'react-hot-toast'

export default function Payment() {
    const { cartId, setTotalCartPrice, setcartProduct, setnumOfCartItems } = useContext(cartContext)

    async function CashPayment() {
        const phoneValue = document.querySelector('#phone').value
        const cityValue = document.querySelector('#city').value
        const detailsValue = document.querySelector('#details').value


        const shippingAddress = {

            "shippingAddress": {
                "details": detailsValue,
                "phone": phoneValue,
                "city": cityValue,
            }
        }

        try {

            const { data } = await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/${cartId}`
                , shippingAddress
                , {
                    headers: { token: localStorage.getItem('tkn') }

                })


            if (data.status === "success") {
                toast.success('Confirm Successful')
                setcartProduct([]); 
                setTotalCartPrice(0);
                setnumOfCartItems(0);
          

            } else {
                toast.error('Error Exist')
            }

            // if(data.status === "success"){
            //     toast.success(data.message,{
            //       position : "top-center",
            //       duration : 1500
            //     })
            //         setcartProduct([]);
            //         setTotalCartPrice(0);
            //         setnumOfCartItems(0);
            //   }
            //   else{
            
                
            //       toast.error("Error Occure...")
                
            //   }

          

        }

        catch (e) {
            console.log("Error", e)
        }


    }

    return (
        <>

            <div className="container py-5 gy-2">
                <form  >
                    <h1 className='text-primary text-center'>Form Cash Payment</h1>
                    <label htmlFor="phone">Phone</label>
                    <input id='phone' type="tel" name="phone" placeholder='Enter Your Phone' className='mb-3 form-control ' />

                    <label htmlFor="city">City</label>
                    <input id='city' type="text" name="city" placeholder='Enter Your City' className='mb-3 form-control' />

                    <label htmlFor="details">Details</label>
                    <textarea id="details" type="text" className='form-control mb-3' placeholder='Type Details' />


                    <button type='button' onClick={CashPayment} className='btn btn-primary m-3 d-flex align-items-center'>

                        Confirm Cash Payment

                    </button>
                </form>

            </div>

        </>
    )
}


