import axios from 'axios'
import React, { useState } from 'react'
import { useContext } from 'react'
import toast from 'react-hot-toast'
import { Circles, ColorRing } from 'react-loader-spinner'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import { cartContext } from '../../context/cartcontext'
import { Helmet } from 'react-helmet'


export default function ProductDetails() {

    const { AddProductToCart } = useContext(cartContext)

    const [sendLoading, setsendLoading] = useState(false)

    const { id } = useParams();

    async function addProducts(id) {
        setsendLoading(true)
        const res = await AddProductToCart(id)

        if (res.status === "success") {
            toast.success(res.message, {
                position: "top-center",
                duration: 1500
            })
        }
        else {


            toast.error("Error Occure...")

        }

        setsendLoading(false)


    }

    function GetProductDetails() {

        return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
    }

    const { data, isLoading, isFetched, isFetching } = useQuery("ProductDetails", GetProductDetails)
    console.log(data)


    if (isLoading) {

        return <div className="vh-100 d-flex justify-content-center align-items-center">

            <ColorRing
                visible={true}
                height="80"
                width="80"
                ariaLabel="blocks-loading"
                wrapperStyle={{}}
                wrapperClass="blocks-wrapper"
                colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
            />

        </div>
    }

    return (
        <>
        
        <Helmet>
        <title>Product Details </title>
    </Helmet> 


            <div className="container py-5">
                <div className=" row align-items-center ">

                    <div className="col-md-3">
                        <figure>
                            <img className="w-100" src={data.data.data.imageCover} alt={data.data.data.title} />
                        </figure>
                    </div>

                    <div className="col-md-9 text-center ">

                        <Helmet>
                            <title>{data.data.data.title.split(' ').slice(0, 2).join(" ")} </title>
                        </Helmet>
                        <div className="details">
                            <h1>{data.data.data.title}</h1>
                            <p className="text-muted">{data.data.data.description}</p>
                            <h5>Price: {data.data.data.price}  EGP</h5>

                            <button onClick={() => addProducts(data.data.data.id)} className="w-100 p-3 rounded-3 border-white main-bg-color text-white">

                                {sendLoading ? <ColorRing
                                    visible={true}
                                    height="40"
                                    width="40"
                                    ariaLabel="blocks-loading"
                                    wrapperStyle={{}}
                                    wrapperClass="blocks-wrapper"
                                    colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
                                /> : " + Add To Cart"}

                            </button>


                        </div>
                    </div>
                </div>
            </div>



        </>
    )
}
