import axios from "axios";
import { createContext, useEffect, useState } from "react";


export const cartContext = createContext()

export function CartContextProvider({ children }) {

    const [cartProduct, setcartProduct] = useState(null)
    const [totalCartPrice, setTotalCartPrice] = useState(0)
    const [numOfCartItems, setnumOfCartItems] = useState(0);
    const [cartId, setCartId] = useState(null)

    async function AddProductToCart(productid) {

        try {
            const { data } = await axios.post('https://ecommerce.routemisr.com/api/v1/cart', {
                "productId": productid
            }, {
                headers: { token: localStorage.getItem('tkn') }
            })

            LoggedUserCart();

            // setnumOfCartItems(data.numOfCartItems)
            // setTotalCartPrice(data.data.totalCartPrice)
            // setcartProduct(data.data.products)

            return data;


        }
        catch (e) {
            console.log("Error", e)
        }
    }


    async function LoggedUserCart() {
        try {


            const { data } = await axios.get('https://ecommerce.routemisr.com/api/v1/cart', {
                headers: { token: localStorage.getItem('tkn') }
            })

            setnumOfCartItems(data.numOfCartItems)
            setTotalCartPrice(data.data.totalCartPrice)
            setcartProduct(data.data.products)
            setCartId(data.data._id)


        }
        catch (e) {
            console.log("Error Exist", e)
        }
    }

    useEffect(() => {

        LoggedUserCart()


    }, [])


    async function RemoveItems(productId) {
        try {


            const { data } = await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, {
                headers: { token: localStorage.getItem('tkn') }
            })

            setnumOfCartItems(data.numOfCartItems)
            setTotalCartPrice(data.data.totalCartPrice)
            setcartProduct(data.data.products)

            return data;
        }
        catch (e) {
            console.log("Error Exist", e)
        }

    }

    async function clearData() {
        try {


            const { data } = await axios.delete('https://ecommerce.routemisr.com/api/v1/cart', {
                headers: { token: localStorage.getItem('tkn') }
            })

            setnumOfCartItems(0)
            setTotalCartPrice(0)
            setcartProduct([])

            return data;
        }
        catch (e) {
            console.log("Error Exist", e)
        }

    }


    async function updateCount(productId, count) {
        try {
            const { data } = await axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`, {
                "count": count
            }, {
                headers: { token: localStorage.getItem("tkn") }
            })

            setcartProduct(data.data.products)
            setTotalCartPrice(data.data.totalCartPrice)
            setnumOfCartItems(data.numOfCartItems)



            return data;

        }

        catch (e) {
            console.log("error", e)
        }

    }



    return <cartContext.Provider value={{clearData, LoggedUserCart, AddProductToCart, numOfCartItems, totalCartPrice, cartProduct, RemoveItems, updateCount, cartId , setTotalCartPrice ,setcartProduct,setnumOfCartItems }}>

        {children}

    </cartContext.Provider>
}