import React from 'react'
// import { authContext } from '../../context/authentaction';
import axios from 'axios';
import { useEffect } from 'react';
import { useState } from 'react';
import { ColorRing } from 'react-loader-spinner';
import { useQuery } from 'react-query';
import Home from '../HomeSlider/Home';
import CategorySlider from '../CategorySlider/CategorySlider';
import { Link } from 'react-router-dom';
import { useContext } from 'react';
import { cartContext } from '../../context/cartcontext';
import toast from 'react-hot-toast';
import { Helmet } from 'react-helmet';


export default function Products() {
  
  const {AddProductToCart} = useContext(cartContext)

  async function addProducts(id){

   const res =  await AddProductToCart(id)
   console.log("Res mn product" , res)

   if(res.status === "success"){
    toast.success(res.message,{
      position : "top-center",
      duration : 1500
    })
  }
  else{

    
      toast.error("Error Occure...")
    
  }

  
   }
 

  function AllProducts() {



    return axios.get('https://ecommerce.routemisr.com/api/v1/products')


  }

  // const { isError, isFetching, isLoading, data , refetch } = useQuery("useQuery", AllProducts , {

  //   enabled: false
  // });


  const { isError, isFetching, isLoading, data, refetch } = useQuery("useQuery", AllProducts);



  console.log(data?.data.data)




  if (isLoading) {

    return <div className="vh-100 d-flex justify-content-center align-items-center">

      <ColorRing
        visible={true}
        height="80"
        width="80"
        ariaLabel="blocks-loading"
        wrapperStyle={{}}
        wrapperClass="blocks-wrapper"
        colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
      />

    </div>
  }




  return <>


      <Helmet>
        <title>Products </title>
    </Helmet> 

    <div className="container p-5 ">

      <div className="row gx-0 mb-5">
        <div className="col-sm-9">
          <Home />
        </div>

        <div className="col-sm-3">
          <img style={{ width: "100%", height: "200px" }} src={require('../../imge/images/grocery-banner.png')} alt="slider" />
          <img style={{ width: "100%", height: "200px" }} src={require('../../imge/images/grocery-banner-2.jpeg')} alt="slider" />
        </div>
      </div>

      <CategorySlider />

      {/* <button onClick={refetch} className="btn btn-success w-100"> Get Products </button> */}

      <div className="row gy-4">

        {data?.data.data.map(function (product, indx) {

          return <div key={indx} className="col-md-2">

            <div className="products gx-5">

              <Link to={`/ProductDetails/${product.id}`}>

              <img src={product.imageCover} className='w-100' alt="product" />
              <h6 className='main-color'>{product.category.name}</h6>
              <h6>{product.title.split(' ').slice(0, 2).join(" ")}</h6>

              <div className="d-flex justify-content-between ">
                <p>{product.price} EGP</p>
                <p><span><i className="fa-solid fa-star star"></i> </span> {product.ratingsQuantity} </p>
              </div>

              </Link>

              

              <button onClick={()=>addProducts(product.id)} className="w-100 p-2 btn   rounded-3 border-white main-bg-color text-white"> + Add To Cart</button>

            </div>






          </div>

        })}



      </div>


    </div>







  </>
  }

