import React from 'react'
import Slider from 'react-slick'
import axios from 'axios';
import { useQuery } from 'react-query';
import { ColorRing } from 'react-loader-spinner';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function CategorySlider() {

function GetAllCategory (){

   return axios.get('https://ecommerce.routemisr.com/api/v1/categories')

    }


  const {data , isLoading} =  useQuery("CategorySlider" , GetAllCategory , {

    refetchOnMount : false
  })

  console.log(data?.data.data)


 

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 7,
        slidesToScroll: 3,
        arrows: false,
        autoplay : 500


    };


    if(isLoading){
        return <div className="vh-100 d-flex justify-content-center align-items-center">

        <ColorRing
          visible={true}
          height="80"
          width="80"
          ariaLabel="blocks-loading"
          wrapperStyle={{}}
          wrapperClass="blocks-wrapper"
          colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
        />
  
      </div>

    }
   


    return (
        <>
            <div className='my-5'>
                <h1 className='text-success'>Category Slider</h1>
                <Slider {...settings}>

                    {data?.data.data.map(function (category , indx){
                    
                    return   <div key={indx}>
                    <img style={{ width: "100%", height: "200px" }} src={category.image} alt="slider" />
                    <h6 className='mt-3'>{category.name}</h6>
                </div>


                    })}
                   


                </Slider>
            </div>
        </>
    )
}
