import React, { useContext } from 'react'
import { cartContext } from '../../context/cartcontext'
import { ColorRing } from 'react-loader-spinner'
import toast from 'react-hot-toast'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'


export default function Cart() {

    const { cartProduct, totalCartPrice, numOfCartItems, RemoveItems, updateCount , clearData } = useContext(cartContext)


    async function updateElement(id, count) {
        const res = await updateCount(id, count)

        if (res.status === "success") {
            toast.success("Update Success")
        } else {
            toast.error("Error occure")
        }



    }

    async function clearAllCart(){
        await clearData();
    }



    if (cartProduct === null) {
        return <div className="vh-100 d-flex justify-content-center align-items-center">

            <ColorRing
                visible={true}
                height="80"
                width="80"
                ariaLabel="blocks-loading"
                wrapperStyle={{}}
                wrapperClass="blocks-wrapper"
                colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
            />

        </div>
    }

    // else{
    //     return <>

    //     <h3>No Data Found in Cart <Link to={'/products'}>Get Some Product</Link></h3>
    //     </>
    // }

    async function deletteItems(id) {
        const res = await RemoveItems(id)

        if (res.status === "success") {
            toast.success("Product Remove Successfuly")

        }
        else {


            toast.error("Error Occure...")

        }



    }





    return <>

     <Helmet>
        <title>Cart </title>
    </Helmet> 


        <div style={{ background: "#eee" }} className="container py-5 my-3 ">




            <h1> Shop Cart </h1>
            <p> Total Items : {numOfCartItems}</p>
            <p className='main-color'>Total Price :{totalCartPrice} </p>
            <div className="div d-flex justify-content-between p-2 ">

                <button onClick={clearAllCart} className='btn btn-danger d-flex align-items-center'>Clear All Product</button>
                <Link to={'/payment'} className='btn btn-primary text-white ' >Confirm Payment</Link >
            </div>






            {cartProduct.map(function (product, idx) {

                console.log(product)
                return <div key={idx} className="row align-items-center m-3 border-bottom bprder-4 p-3  ">

                    <div className="col-sm-1">

                        <img className='w-100' src={product.product.imageCover} alt="items" />

                    </div>

                    <div className="col-sm-9 ">
                        <h6>{product.product.title} </h6>
                        <h6>Price : {product.price}</h6>
                        <button onClick={() => deletteItems(product.product.id)} className="btn-outline-danger btn"> Remove </button>

                    </div>


                    <div className="col-sm-2 d-flex align-items-center">

                        <button onClick={() => updateElement(product.product.id, product.count + 1)} className='btn btn-outline-success'>+</button>
                        <span className='mx-2'>{product.count}</span>
                        <button onClick={() => updateElement(product.product.id, product.count - 1)} className='btn btn-outline-danger'>-</button>

                    </div>


                </div>
            })}










        </div>
    </>

}
