import React, { useState } from 'react'
import {useFormik} from 'formik'
import axios from "axios"
import { useNavigate } from 'react-router-dom'
import { Vortex} from 'react-loader-spinner'

export default function Register() {

let user = {
  name: "",
  email: "",
  password: "",
  rePassword: "",
  phone: ""
}
const navigte = useNavigate();
const [errMsge, seterrMsge] = useState("")
const [successMsge, setsuccessMsge] = useState("")
const [isLoading, setisLoading] = useState(false)
// let baseUrl = "https://ecommerce.routemisr.com"


 
async function registerNewUser (values){

  setisLoading(true);
 

  try {
    
    // let { data } = await axios.post(`${baseUrl}/api/v1/auth/signup`, values)
    const { data } = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', values)
    console.log(data)

    if(data.message === "success") {

      setsuccessMsge("Account Created Successfuly");
      setTimeout(function(){

        navigte('/login');

      }, 1000);

     
    }

  }

  catch (err){
  
 console.log( "eroor exist", err.response.data.message)

 seterrMsge(err.response.data.message)

  }
  
  
  setisLoading(false);

}



const formikObj = useFormik({

 initialValues : user,

 onSubmit : registerNewUser,

 validate : function(values){
  
 const errors = {};

 if(values.name.length < 3 || values.name.length > 12) {
  
  errors.name = ("Name must be greater than 3 characters and less than 12 characters ")

 }

 if( values.email.includes("@") === false || values.email.includes(".com") === false) {

  errors.email = ("Email invalid")
 }

 if(values.password.length < 6 || values.password.length > 12) {

  errors.password = ("Password must be greater than 7 charachters ")
 }

 if(values.rePassword !== values.password) {
 
 errors.rePassword = ("rePassword and Password must be match ")
 }

 if( ! values.phone.match(/^(02)?01[0125][0-9]{8}$/)) {

  errors.phone = ("Phone Invalid")
 }
console.log(errors  )
 return errors ;

 },

})



  return ( <>
  
  <div className="  w-75 m-auto py-5">
   
   <h3 className="text-success  " > Register Now : </h3 >

   {errMsge ? <div className="alert alert-danger"> {errMsge}</div> : ''}

   {successMsge ? <div className="alert alert-success"> {successMsge}</div> : ''}
    
   

     <form onSubmit={formikObj.handleSubmit} >

              <label htmlFor='name' className="m-2"> Name : </label>

            <input onBlur={formikObj.handleBlur} value={formikObj.values.name} onChange={formikObj.handleChange}  id='name' className="form-control  bg-light  " placeholder='Enter your name' type="name" />
                {formikObj.errors.name && formikObj.touched.name ? <div className="alert alert-danger w-75 m-2">{formikObj.errors.name}</div> : ""}

            <label htmlFor='email' className="m-2"> Email : </label>

             <input onBlur={formikObj.handleBlur} value={formikObj.values.email} onChange={formikObj.handleChange} id='email' className="form-control  bg-light " placeholder='Enter your email' type="email" />
             
             {formikObj.errors.email && formikObj.touched.email ? <div className="alert alert-danger w-75 m-2">{formikObj.errors.email}</div> : ""}

            <label htmlFor='password'className="m-2" > Password : </label>

              <input onBlur={formikObj.handleBlur} value={formikObj.values.password} onChange={formikObj.handleChange} id='password' className="form-control  bg-light " placeholder='Enter your password' type="password" />
              {formikObj.errors.password && formikObj.touched.password ? <div className="alert alert-danger w-75 m-2">{formikObj.errors.password}</div> : ""}

            <label htmlFor='rePassword' className="m-2"> rePassword : </label>

           <input  onBlur={formikObj.handleBlur} value={formikObj.values.rePassword} onChange={formikObj.handleChange} id='rePassword' className="form-control  bg-light " placeholder='Enter your rapassword' type="password" />
           {formikObj.errors.rePassword && formikObj.touched.rePassword ? <div className="alert alert-danger w-75 m-2">{formikObj.errors.rePassword}</div> : ""}

            <label htmlFor='phone' className="m-2"> Phone : </label>

             <input onBlur={formikObj.handleBlur}   value={formikObj.values.phone} onChange={formikObj.handleChange} id='phone' className="form-control  bg-light " placeholder='Enter your phone' type="tel" />  
             {formikObj.errors.phone && formikObj.touched.phone ? <div className="alert alert-danger w-75 m-2">{formikObj.errors.phone}</div> : ""}

             <button type="submit" disabled = { formikObj.isValid == false || formikObj.dirty == false}  className="btn btn-success m-3">
              
                  {isLoading ? <Vortex
                      visible={true}
                      height="20"
                      width="60"
                      ariaLabel="vortex-loading"
                      wrapperStyle={{}}
                      wrapperClass="vortex-wrapper"
                      colors={['red', 'green', 'blue', 'yellow', 'orange', 'purple']}/> : "Register"}              
 

              
            
              
              </button>  

    </form>





  </div>
  
  
  </>
   
  )
}

