import React from 'react'


import { RouterProvider, createBrowserRouter } from 'react-router-dom'
import Layout from './Component/Layout/Layout'
import Products from './Component/Products/Products'
import Login from './Component/Login/Login'
import Register from './Component/Register/Register'
import Logout from './Component/Logout/Logout'
import Category from './Component/Category/Category'
import Brands from './Component/Brands/Brands'
import Notfound from './Component/Notfound/Notfound'
import { AuthProvider } from './context/authentaction'
import ProtectRoute from './Component/Protect/ProtectRoute'
import { QueryClient, QueryClientProvider } from 'react-query'
import ForgetPassword from './Component/ForgetPassword/ForgetPassword'
import ResetPassword from './Component/ResetPassword/ResetPassword'
import ProductDetails from './Component/ProductDetails/ProductDetails'
import { CartContextProvider } from './context/cartcontext'
import { Toaster } from 'react-hot-toast'
import Cart from './Component/Cart/Cart'
import Payment from './Component/PaymentAddress/Payment'
import AllProducts from './Component/AllProducts/AllProducts'
// import { AuthProvider } from './context/authentaction'

import { Offline, Online } from "react-detect-offline";



const router = createBrowserRouter([

  {
    path: '/', element: <Layout />, children: [


      {
        index: true, element:

          <ProtectRoute>
            <Products />
          </ProtectRoute>
      },

      {
        path: 'products', element:
          <ProtectRoute>
            <Products />
          </ProtectRoute>
      },


      {
        path: 'category', element:

          <ProtectRoute>
            <Category />
          </ProtectRoute>
      },

      {
        path: 'brands', element:

          <ProtectRoute>
            <Brands />
          </ProtectRoute>
      },

      {
        path: 'ProductDetails/:id', element:

          <ProtectRoute>
            <ProductDetails />
          </ProtectRoute>
      },


      {
        path: 'cart', element:

          <ProtectRoute>
            <Cart />
          </ProtectRoute>
      },

      {
        path: 'payment', element:

          <ProtectRoute>
            <Payment />
          </ProtectRoute>
      },

      {
        path: 'AllProducts', element:

          <ProtectRoute>
            <AllProducts />
          </ProtectRoute>
      },




      { path: 'login', element: <Login /> },

      { path: 'register', element: <Register /> },

      { path: 'ForgetPassword', element: <ForgetPassword /> },
      { path: 'ResetPassword', element: <ResetPassword /> },

      { path: 'logout', element: <Logout /> },

      { path: '*', element: <Notfound /> },




    ]
  }

])

export default function App() {
  let clientQuery = new QueryClient();

  return <>

    <QueryClientProvider client={clientQuery}>

      <CartContextProvider >
        <AuthProvider>
          <RouterProvider router={router} />
        </AuthProvider>

      </CartContextProvider>

      <Toaster />
    </QueryClientProvider>


    <Offline>

    </Offline>
  </>

}


