import React, { useContext } from 'react'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import logoimge from '../../imge/images/freshcart-logo.svg'

import { authContext } from '../../context/authentaction';
import { cartContext } from '../../context/cartcontext';
export default function Navbar() {

  const { token, setToken } = useContext(authContext);
  const { numOfCartItems } = useContext(cartContext)
  const nav = useNavigate();

  function logoutpro() {
    localStorage.removeItem('tkn')
    setToken(null);
    nav('/login ')
  }




  return (

    <nav className="navbar navbar-expand-lg bg-body-tertiary">
      <div className="container">

        <Link className="navbar-brand" to="/">

          <img src={logoimge} alt="freshcart" />

        </Link>

        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">

          <ul className="navbar-nav me-auto mb-2 mb-lg-0">

            {token ? <>

              <li className="nav-item">
                <Link className="nav-link active main-color" aria-current="page" to="/products" >Products</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link main-color" to="/category" >Category</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link main-color " to="/brands" >Brands</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link main-color " to="/AllProducts" >All Product</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link position-relative main-color" to="/cart " >

                  Cart


                </Link>
              </li>

            </> : ""}


          </ul>



          <ul className="navbar-nav ms-auto mb-2 mb-lg-0 align-items-center ">



           


            {token ? <><div className="d-flex">

              <div className=' mb-2 mb-lg-0 align-items-center position-relative m-2'>

                <i className="fa-solid fa-cart-shopping text-success fa-2xl m-3  "></i>
                <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">
                  {numOfCartItems}
                  <span className="visually-hidden">unread messages</span>
                </span>

              </div>

              <li className="nav-item">
                <span onClick={logoutpro} style={{ cursor: 'pointer' }} className="nav-link text-success">Logout</span>
              </li>

            </div>
             

            </> : <>

              <li className="nav-item">
                <Link className="nav-link" to="/login" >Login</Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link" to="/register" >Register</Link>
              </li>

            </>}






          </ul>



        </div>
      </div>
    </nav>
  )
}
