import axios from 'axios'
import { useFormik } from 'formik'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function ResetPassword() {
    let nav = useNavigate()
    let baseUrl = "https://ecommerce.routemisr.com"
    let validationSchema = Yup.object({
        email: Yup.string().required("Email Required").email("enter Valid Email"),
        newPassword: Yup.string().required("Passowrd Required").matches(/^[A-Z][a-z0-9]{3,16}$/, "enter Valid Passowrd"),
    })
    let ResetPasswordForm = useFormik({
        initialValues: {
            email: "",
            newPassword: ""
        },
        validationSchema,
        onSubmit: ResetPassowrd

    })

    async function ResetPassowrd(val) {

        let { data } = await axios.put(`${baseUrl}/api/v1/auth/resetPassword`, val)
        console.log(data);
        if(data.token){
            nav('/login')
        }
    }
    return (
        <div>

           <div className="container w-75 my-3 ">

           <form  onSubmit={ResetPasswordForm.handleSubmit}>
                <label className='m-2'  htmlFor="email">Email</label>
                <input   onChange={ResetPasswordForm.handleChange} type="email" className=' m-2 form-control' name="email" id="email" />
                <p className='text-danger'>{ResetPasswordForm.errors.email}</p>
                <label className='m-2' htmlFor="newPassword">newPassword</label>
                <input  onChange={ResetPasswordForm.handleChange} type="password" className='form-control n-2' name="newPassword" id="newPassword" />
                <p className='text-danger'>{ResetPasswordForm.errors.newPassword}</p>
                <button  className='btn btn-success m-2'> Reset Password</button>
            </form>


           </div>

        </div>
    )
}
