import axios from 'axios'
// import { data } from 'jquery'
import React, { useState } from 'react'
import { useEffect } from 'react'
import { Helmet } from 'react-helmet'
import { ColorRing } from 'react-loader-spinner'
import { useQuery } from 'react-query'

export default function Brands() {

  


  useEffect(() => {

    AllBrands();
    
    
  }, [])

 function AllBrands() {



      return axios.get('https://ecommerce.routemisr.com/api/v1/brands')
    
    
  }


  const { data , isLoading } = useQuery("Brands", AllBrands)


  if (isLoading) {

    return <div className="vh-100 d-flex justify-content-center align-items-center">

      <ColorRing
        visible={true}
        height="80"
        width="80"
        ariaLabel="blocks-loading"
        wrapperStyle={{}}
        wrapperClass="blocks-wrapper"
        colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
      />

    </div>

  }


  return <>

     <Helmet>
        <title>Brands </title>
    </Helmet> 

    <div className="container p-5">

      <h1 className='text-center main-color p-2'>All Brands </h1>
      <div className="row gy-5">

        {data?.data.data.map(function (item, idx) {

          return <div  key={idx} className="brnds col-sm-3 rounded-3 border-3">

            <img className='w-100' src={item.image} alt="items" />
            <h4 className="m-2 text-center  ">{item.name}</h4>


          </div>

        })}






      </div>
    </div>


  </>
}