import React, { useEffect, useState } from 'react'
import { jwtDecode } from "jwt-decode";
import axios from 'axios'
import { ColorRing } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';



export default function AllProducts() {
    const [userProduct, setUserProduct] = useState(null)
    useEffect(() => {

        const res = jwtDecode(localStorage.getItem('tkn'))
        UserOrder(res.id)


    }, [])

    async function UserOrder(id) {
        try {
            const { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/${id}`)

            console.log(data)

            setUserProduct(data)

        } catch (e) {
            console.log("Error", e)

        }
    }

    if (userProduct === null) {
        return <div className="vh-100 d-flex justify-content-center align-items-center">

            <ColorRing
                visible={true}
                height="80"
                width="80"
                ariaLabel="blocks-loading"
                wrapperStyle={{}}
                wrapperClass="blocks-wrapper"
                colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
            />

        </div>
    }



    return <>

        <Helmet>
            <title>All Product </title>
        </Helmet>

        <div className="container">
            <div className="row g-4">

                {userProduct.map(function (order, idx) {
                    return <div key={idx} className="col-md-6">

                        <div style={{ background: "#eee" }} className=" rounded-4 order m-4 text-center p-2">

                            <div className="container">
                                <div className="row">

                                    {order.cartItems?.map(function (item, iedx) {
                                        return <div key={iedx} className="col-sm-4">
                                            <div className='  my-2'>
                                                <img src={item.product.imageCover} className='w-50' alt="" />
                                                <h6>{item.product.title.split(' ').slice(0, 2).join(" ")}</h6>
                                                <h5 className='main-color'> Count : {item.count}</h5>
                                                <h5 className='main-color'>Price : {item.price}</h5>


                                            </div>


                                        </div>
                                    })}

                                </div>
                            </div>


                            <p>Order Sent with phone : {order.shippingAddress.phone}</p>
                            <p>Payment Method : {order.shippingAddress.details}</p>
                            <p>Addres : {order.shippingAddress.city}</p>
                            <p className='main-color'>Total Price : {order.totalOrderPrice}</p>



                        </div>
                    </div>



                })}


            </div>
        </div>
    </>

}

